@font-face {
  font-family: 'icomoon';
  src: url('../../assets/fonts/icons/icomoon.eot?286x7a');
  src: url('../../assets/fonts/icons/icomoon.eot?286x7a#iefix') format('embedded-opentype'),
    url('../../assets/fonts/icons/icomoon.ttf?286x7a') format('truetype'),
    url('../../assets/fonts/icons/icomoon.woff?286x7a') format('woff'),
    url('../../assets/fonts/icons/icomoon.svg?286x7a#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class*="icon-"]:before,
.icon-extension {
  display: inline-block;
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

