// form box

.form-box {
  @extend .box;
  padding: $spacing;
}

// fieldset

fieldset > div:first-of-type {margin-top: 0.25rem;}

// form group structure

label,
span.label,
p.label {font-weight: 400;}

div.form-group {
  position: relative;
  margin: $vmargin 0;

  &.newsletter-request {
    text-align: left;
    span.text {font-size: $fs-18;}
  }

  span.label,
  p.label {
    display: block;
    margin-bottom: 0.25rem;
  }

  span.field {
    position: relative;
    display: block;
    margin: 0;

    &:before { // icons
      @extend .icon-extension;
      position: absolute;
      top: 0; right: 0.75rem;
      font-size: $fs-16;
      line-height: 2.8;
      z-index: 2;
    }

    input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
    textarea,
    select {
      width: 100%;
      
      @include bp-m {
        &.size-small {width: $ff-s;}
        &.size-medium {width: $ff-m;}
        &.size-large {width: $ff-l;}
      }
    } 

    // connected field & button

    &.field-button {
      display: flex;

      input {flex: 1 1 auto; border-right-color: transparent!important;}
      button {flex: 0 0 auto;}
    }
  }
}

// form elements

input,
textarea,
select,
button {
  font-family: $ff-1;
  font-size: $fs-20;
  line-height: $lh-general;
}

input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
textarea,
.custom-select {
  border: 1px solid $clr-fields;
  border-radius: 0.25rem;
  padding: 0.6rem 0.75rem;
  background: white;
  color: $clr-1;
  box-sizing: border-box;
  box-shadow: none;
  outline: none;
  -webkit-appearance: none;

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    // IE 10/11 only hack
    padding-top: 0.68rem;
    padding-bottom: 0.68rem;
  }

  &:focus {
    box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
    outline: none;
  }
}

textarea {
  display: block;
  width: 100%;
  height: 9.25rem;
}

::-webkit-input-placeholder {color: $clr-placeholder;}
::-moz-placeholder {color: $clr-placeholder; opacity: 1;}
:-ms-input-placeholder {color: $clr-placeholder!important;}
::-ms-input-placeholder {color: $clr-placeholder!important;}
::placeholder {color: $clr-placeholder;}

// custom radio & checkbox buttons

label.custom-control {
  position: relative; // required for HTML error message position
  display: flex;
  align-items: flex-start;
  cursor: pointer;

  span {
    position: relative;
    flex: 0 0 auto;
    content: "";

    &.input {margin-right: 0.5rem;}

    &.text {
      display: block;
      flex: 0 1 auto;
    }
    
    // radio

    &.radio {
      display: block;
      border: 1px solid $clr-fields;
      border-radius: 50%;
      width: 25px;
      height: 25px;
      background: white;

      &:before {
        position: absolute;
        top: 4px; left: 4px;
        border-radius: 50%;
        width: 13px;
        height: 13px;
        background: $clr-b1;
      }
    }
    
    // checkbox

    &.checkbox {
      display: block;
      border: 1px solid $clr-fields;
      border-radius: 2px;
      width: 23px;
      height: 23px;
      background: white;

      &:before {
        @extend .icon-extension;
        position: absolute;
        top: 3px; left: 3px;
        font-size: $fs-16;
        color: $clr-b1;
        content: none;
      }
    }
  }

  input:not(#iefix) {  
    @include hide-screen();
    top: 29px; left: 12px; // required for HTML error message position

    &:checked {
      ~ span.radio:before {content: "";}
      ~ span.checkbox:before {@extend .icon-check;}
    }

    &:focus {
      ~ span.radio,
      ~ span.checkbox {box-shadow: 0 0 0 5px rgba($clr-fields,0.25);}
    }
  }
}

// compact form

div.compact-form {
  margin-bottom: $vmargin;

  div.form-group {
    margin: 0.75rem 0 0;

    span.field {
      position: relative;

      input {
        position: relative;
        z-index: 2;
        border-radius: 0.25rem 0 0 0.25rem!important;
        padding: 0.5rem 0.75rem!important;
        width: 45px!important; // stops breaking the layout on small screens
        font-size: $fs-18;

        &:focus {
          box-shadow: none!important;

          + button {
            &:after {content: "";}

            + span.statement {
              right: 0; left: 0;
              opacity: 1;
              transition: left 0s, right 0s, opacity 0.25s;
            }       
          }
        }
      }

      button {
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.5em 0.75em;
        font-size: $fs-18;
        box-shadow: none;

        &:active {
          background: $clr-b1;
          box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
        }

        &:after {
          position: absolute;
          top: 0; left: 0; bottom: 0; right: 0;
          z-index: 1;
          border-radius: 0.25rem;
          box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
        }
      }

      span.statement {
        position: absolute;
        bottom: 100%; left: -100em; right: 100em;
        z-index: 3;
        margin-bottom: 0.5rem;
        border-radius: 0.5rem;
        padding: 0.5rem 1rem;
        background: $clr-2;
        font-size: $fs-14;
        text-align: center;
        color: white;
        opacity: 0;
        transition: left 0s 0.5s, right 0s 0.5s, opacity 0.25s 0.25s;
        
        @include bp-l {
          span {
            footer & {display: block;}
          }
        }

        a {color: white;}

        &:after {
          position: absolute;
          top: 100%; left: 50%;
          margin-left: -8px;
          border: 8px solid transparent;
          border-top-color: $clr-2;
          content: "";
        }
      }
    }
  }
}

// reveal fields

div.reveal-fields {
  text-align: left;

  input#reveal-fields:not(#iefix) {
    @include hide-screen();

    &:checked {
      + label.button-reveal {
        display: none;

        &:before {@extend .icon-minus-circle;}
        + div.form-group {display: block;}
      }
    }

    &:focus {
      + label.button-reveal {
        box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
      }
    }
  }

  label.button-reveal {
    display: inline-block;
    border: 1px solid $clr-fields;
    border-radius: 0.25rem;
    padding: 0.25rem 0.5rem;
    background: rgba(black,0.05);
    text-align: center;
    font-size: $fs-16;
    color: #555;
    cursor: pointer;

    div.contact-form-inline & {
      border-color: #111;
      background: rgba(white,0.05);
      color: $clr-3;

      @include bp-m {
        &:hover {background: rgba(white,0.1);}
      }
    }

    @include bp-m {
      &:hover {background: rgba(black,0.1);}
    }
  }

  div.form-group {display: none;}
}
