// landing content

div.landing-content {
  //> p:nth-of-type(1) {@extend .lead;}
  > ul:not([class]):not([class]) {@extend .disc;}
  > ol:not([class]) {@extend .decimal;}
  @include bp-m {> :last-child {margin-bottom: 0;}}
}

// header selling point

ul.selling-points {
  position: relative;
  z-index: 4;
  margin: 0;
  font-size: $fs-18;
  color: $clr-1;

  @include bp-m {
    display: flex;
    align-items: center;
  }

  > li {
    @include bp-m {flex: 1 0 auto;}

    &.one {@include bp-l-less {@include hide-screen();}}

    > span,
    a {
      display: inline-block;
      padding: $nav-link-padding 1rem;
      font-weight: 500;
    }

    a {
      background: $clr-4;
      color: $clr-b1;
      text-decoration: none;
      
      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-b1;
          color: white;
        }

        &:active {box-shadow: inset 0 1px 2px rgba(0,0,0,0.75);}

        &:before {
          margin-right: 0.5rem;
          font-size: $fs-18;
        }
      }
      
      span {
        position: absolute;
        left: -1000em;

        @include bp-m {position: static;}
      }
    }
  }
}

// quote

blockquote.landing-quote {
  position: relative;
  border-radius: $border-radius;
  border: 0;
  padding: $spacing;
  background: $clr-2;
  font-style: italic;
  font-size: $fs-18;
  color: white;

  &:after {
    position: absolute;
    top: 100%; right: 40%;
    border: 10px solid;
    border-color: $clr-2 transparent transparent $clr-2;
    content: "";
  }

  > :last-child {margin-bottom: 0;}
}

div.team-quote {
  font-size: $fs-18;

  figure {
    display: block;
    position: relative;
    overflow: hidden;
    margin: 0 auto $vmargin;
    border-radius: 50%;
    max-width: 200px;

    img {
      position: absolute;
      top: 0; left: 0;
      border-radius: 50%;
      width: 100%;
    }

    &:before {
      display: block;
      padding-top: 100%;
      content: "";
    }
  }

  p {
    text-align: center;

    span {
      display: block;
      margin-top: 0.1rem;
      font-size: $fs-16;
    }
  }
}
