header.banner {
  position: fixed;
  left: 0; top: 0; right: 0;
  z-index: 999;
  padding: 0;

  div.wrap {
    padding: 0;
    @include bp-m {
      padding: 0 ($gutter*2);
    }
  }
}

// no js menu mechanism

input.mobile-menu:not(#iefix) {
  @include hide-screen();

  + p {
    flex: 0 1 auto;
    margin: 0;
    line-height: $lh-general;
    font-size: $fs-18;
    cursor: pointer;

    @include bp-m {display: none;}

    label {
      display: block;
      margin: 0;
      padding: $nav-link-padding 1.25rem;
      line-height: $lh-general;
      font-size: $fs-18;
      cursor: pointer;

      &:before {
        @extend .icon-extension,.icon-menu;
        line-height: $lh-general;
        font-size: $fs-18;
        color: $clr-1;
      }

      span {
        @include hide-screen();
      }
    }
  }

  &:checked + p {

    &:after { // bit of a hack to include a background for the logo and nav links on mobile
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 2;
      background: white;
      content: "";

      @include bp-m {content: none;}
    }

    label {
      position: relative;
      z-index: 4;
      &:before {@extend .icon-times;}
    }

    + div.links {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;

      &:before { // used to create space for the fixed header when menu is open
        display: block;
        padding: $nav-link-padding 0;
        line-height: $lh-general;
        font-size: $fs-18;
        visibility: hidden;
        content: "\00A0";
      }
    }
  }
}

// navigation

header.banner nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  position: relative;

  p.logo {
    flex: 1 1 auto;
    position: relative;
    z-index: 3;
    margin: 0 0 0 ($gutter/2);
    
    @include bp-m {
      margin: 0;
    }

    a, > span {
      display: block;
      width: 225px;
      background: url(../../assets/images/logos/mantra-systems.svg) no-repeat 50% 100%; 
      background-size: 100% auto;

      @include bp-l {width: 250px;}

      &:before {
        display: block;
        padding-top: 13%;
        content: "";
      }

      > span {@include hide-screen();}
    }
  }

  label {flex: 0 1 auto;} // what is this?

  div.links {
    position: absolute;
    left: -1000em;
    background: $clr-1;

    @include bp-m {
      position: static;
      display: flex;
      align-items: center;
      flex: 0 1 auto;
      margin: 0;
      padding: 0;
      width: auto;
      background: none;
    }

    ul.site {
      position: relative;
      margin: 0;
      text-align: left;
      font-size: $fs-18;

      @include bp-m {
        display: flex;
        align-items: center;
      }

      > li {
        overflow: hidden;
        margin: 1rem;
        border-radius: 0.25rem;

        @include bp-m {
          flex: 1 0 auto;
          margin: 0;
          border-radius: 0;
        }

        > a,
        > label {
          display: block;
          padding: 0.5rem 0.75rem;
          background: rgba(black,0.4);
          font-weight: 500;
          color: white;
          text-decoration: none;
          user-select: none;
          transition: background-color 0.1s, color 0.1s, box-shadow 0.1s;

          @include bp-m {
            flex: 1 0 auto;
            margin: 0;
            border-radius: 0;
            padding: $nav-link-padding 0.4rem;
            background: none;
            color: $clr-1;

            &:hover,
            &:focus {
              color: $clr-b1;
              box-shadow: inset 0 5px $clr-b1;
            }

            &.current {
              box-shadow: inset 0 5px $clr-b1;
              color: $clr-b1;
            }
          }

          @include bp-l {
            padding: $nav-link-padding 0.8rem;
          }

          span {
            @include bp-l-less {position: absolute; left: -1000em;}
            @include bp-m-less {position: static;}
          }
        }
        
        // solutions sub nav

        div.sub {

          @include bp-m {
            overflow: hidden;
            position: absolute;
            top: -1000em; left: 0; right: 0;
            margin-top: -0.5rem;
            border-radius: 0 0 0.5rem 0.5rem;
            background: $clr-2;
            box-shadow: 0 3px 3px rgba(black,0.2);
            opacity: 0;
            transition: opacity 0.1s, top 0.1s 0.1s, margin-top 0.1s;
          }

          div.row > div:nth-child(1) {margin-top: 0;}

          div.row > div:nth-child(2) {
            @include bp-m {background: rgba(white,0.05);}
          }

          div.col {
            overflow: hidden;
            margin-top: 1rem;
            border-radius: 0.25rem;

            @include bp-m {
              margin-top: 0;
              border-radius: 0;
            }

            p,ul {
              margin: 0;

              a {
                display: block;
                padding: 0.5rem 0.75rem;
                text-decoration: none;
                color: white;
                transition: background-color 0.2s, color 0.1s;
              }
            }

            p {
              font-size: $fs-18;

              &.sub-1 {background: $clr-b2;}

              &.sub-2 {
                background: $clr-b3;

                + ul {
                  overflow: hidden;
                  border-radius: 0 0 0.25rem 0.25rem;

                  @include bp-m {
                    overflow: visible;
                    border-radius: 0;
                  }
                }
              }

              &.sub-3 {
                margin-top: 1rem;
                border-radius: 0.25rem 0.25rem 0 0;
                background: $clr-b4;

                @include bp-m {
                  margin-top: 0;
                  border-radius: 0;
                }
              }

              a {
                font-weight: 500;

                @include bp-m {
                  &:hover,
                  &:focus {background: rgba(black,0.2);}
                }
              }
            }

            ul li {
              border-top: 1px solid rgba(black,0.1);
              background: rgba(white,0.05);

              @include bp-m {
                border-top: 0;
                background: none;

                a {

                  &:hover,
                  &:focus {
                    background: rgba(white,0.075);
                  }
                
                  &.current {
                    background: rgba(black,0.3);
                    box-shadow: inset 5px 0 $clr-b1;
                  }
                }
              }

              ul li {background: none;}
            }
          }
        }

        > input:not(#iefix) {
          @include hide-screen();

          + label {
            display: none;
            cursor: pointer;
            @include bp-m {display: block;}
          }

          &:checked + label {
            box-shadow: inset 0 5px $clr-b1, inset 0 -5px $clr-b1;
            color: $clr-b1;
            transition: color 0.1s, box-shadow 0.1s 0.15s;

            + div.sub {
              top: 100%;
              margin-top: 0;
              opacity: 1;
              transition: opacity 0.15s 0.1s, margin-top 0.15s 0.1s;
            }
          }
        }
      }
    }

    ul.social {
      margin-bottom: 1rem;
      text-align: center;
      font-size: $fs-30;

      @include bp-m {
        @include hide-screen();
      }

      li {
        display: inline-block;

        a {
          display: block;
          padding: 0.5rem;
          color: white;

          span {@include hide-screen();}
        }
      }
    }
  }
}
