// main

main.content {
  background: white;

  @keyframes pageFade {
    0% {background-color: white; z-index: 9999;}
    50% {background-color: white;}
    100% {background-color: transparent; z-index: -1;}
  }

  &:after {
    position: fixed;
    top: 0; left: 0; bottom: 0; right: 0;
    z-index: -1;
    background-color: white;
    content: "";
    animation: pageFade 1s ease 0s 1 normal forwards;
  }
}
