html {
  font-size: $fs-base;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  background: $clr-3;
  text-align: left;
  font-family: $ff-1;
  font-weight: 400;
  line-height: $lh-general;
  color: $clr-1;
  
  &:before { // this covers the fake nav background and box-shadow
    position: relative;
    z-index: 998;
    display: block;
    background: white;
    padding: $nav-link-padding 0 ($nav-link-padding + 0.17);
    line-height: $lh-general;
    font-size: $fs-18;
    content: "\00A0";
  } 

  &.article:before,
  &.consulting:before,
  &.training:before,
  &.guide:before,
  &.landing:before {padding: $nav-link-padding 0;} // extra 0.17 padding not required on these pages

  &:after { // this is the fake nav background
    position: fixed;
    left: 0; top: 0; right: 0;
    z-index: 997;
    background: white;
    padding: $nav-link-padding 0;
    line-height: $lh-general;
    font-size: $fs-18;
    content: "\00A0";
    box-shadow: 0 1px 2px rgba(black,0.2);
  } 
}

// hiding

.hide-screen {
  @include hide-screen();

  &.focusable:active,
  &.focusable:focus {
    position: static;
    overflow: visible;
    clip: auto;
    margin: 0;
    width: auto;
    height: auto;
  }
}

.hide-mobile {
  @include bp-s-less {@include hide-screen();}
}

.hide {display: none!important;}

// interactive element focus

a,
button,
input,
select,
textarea {
  &:focus {outline: 1px dotted;}
}

// elements

embed,
iframe,
object {max-width: 100%;}

sup {
  font-size: 0.75rem;
  vertical-align: super;
}

code {
  font-family: monospace;
  line-height: 1.0;
}

figcaption {font-size: $fs-16;}
strong,b {font-weight: 700;}

dl {
  dt {
    margin-top: 0.75rem;

    &:first-child {margin-top: 0;}
  }
}

q {font-style: italic;}

blockquote {
  border-left: 5px solid $clr-b1;
  padding-left: $spacing;

  p {font-style: italic;}
}

// anchors

a {
  color: $clr-b1;
  text-decoration: underline;

  @include bp-m {
    &:hover,
    &:focus {text-decoration: none;}
  }
}

// buttons

.button.button { 
  display: inline-block;
  border-radius: 0.25rem;
  border: 0;
  padding: 0.5em 1.25em;
  background-image: linear-gradient(to bottom, #999 0%, #555 100%);
  background-blend-mode: overlay;
  background-color: $clr-b1;
  text-align: center;
  font-family: $ff-1;
  font-weight: 600;
  line-height: inherit;
  color: white;
  cursor: pointer;
  text-decoration: none;
  text-shadow: none;
  box-shadow: 0 1px 2px rgba(0,0,0,0.4);
  outline: none;
  -webkit-appearance: none;
  transition: box-shadow 0.1s, background-color 0.1s;

  &[class*="icon-"] {
    &:before {
      margin-right: 0.5rem;
      vertical-align: text-top;
    }
  }
  
  &.small,
  main div.col-4 form & {font-size: $fs-16;}

  &.big {font-size: $fs-30;}

  &.secondary {
    background: none;
    box-shadow: inset 0 0 0 2px white, 0 1px 2px rgba(0,0,0,0.4);

    @include bp-m {
      &:hover,
      &:focus {box-shadow: inset 0 0 0 2px white, 0 0 0 5px rgba(black,0.1);}

      &:active {
        background: white;
        color: $clr-1;
        box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
      }
    }
  }

  &.guide-nav-jump {
    padding: 0.25em 0.75em;
    background-color: $clr-4;
    font-weight: 500;
    font-size: $fs-16;
    color: $clr-1;

    &:before {
      margin-right: 0.25rem;
      font-size: $fs-14;
      color: $clr-1;
    }
  }

  @include bp-m {
    &:hover,
    &:focus {box-shadow: 0 0 0 5px rgba(black,0.1);}

    &:active {
      background: $clr-b1;
      box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
    }
  }

  &.disabled {
    background: rgba(black,0.1);
    color: rgba(black,0.5);
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
    opacity: 0.5;
  }
}

// alignment

p, pre, blockquote, ul, ol, dl, figure, table {margin-bottom: $vmargin;}

// headings

h1,h2,h3,h4,h5,h6 {
  margin-bottom: $vmargin;
  font-weight: 300;
  font-size: $fs-20;
  line-height: $lh-headings;
  font-family: $ff-1;
}

h1,.h1 {
  font-size: $fs-35;
  
  @include bp-s {
    font-size: $fs-40;
    span {display: block;}
  }

  @include bp-m {font-size: $fs-50;}
  @include bp-l {font-size: $fs-70;}

  body.error main & {
    @extend .h2;
    span {
      display: block;
      font-size: $fs-70;
      @include bp-m {font-size: $fs-100;}
    }
  }
}

.h1-2 {
  font-size: $fs-40;
  @include bp-s {font-size: $fs-45;}
  @include bp-m {font-size: $fs-55;}
}

h2,.h2 {
  font-weight: 300;
  font-size: $fs-25;
  @include bp-s {font-size: $fs-32;}
  @include bp-m {font-size: $fs-30;}
  @include bp-l {font-size: $fs-35;}
}

h3,.h3 {
  font-weight: 400;
  font-size: $fs-22;
  @include bp-m {font-size: $fs-27;}
}

h4,.h4 {
  font-weight: 500;
  @include bp-m {font-size: $fs-25;}
}

h5,.h5 {
  font-weight: 500;
  @include bp-m {font-size: $fs-22;}
}

h6,.h6 {font-weight: 700;}

// helpers

.break {display: block;}
.spacer {margin-top: ($vmargin * 2);}
.no-margin {margin: 0;}
.no-wrap {white-space: nowrap;}
.align-center {text-align: center;}
.align-right {text-align: right;}
.small-text {font-size: $fs-16;}
.new-window {cursor: alias;}

.lead {
  font-weight: 300;
  font-size: $fs-22;
  @include bp-m {font-size: $fs-25;}
}

.target:target:before {
  display: block;
  margin-top: -4rem;
  height: 4rem;
  content: "";
  visibility: hidden;
}

p.skip {
  position: absolute;
  top: 0; left: 0; right: 0;
  z-index: 999999;
  text-align: center;
}

p.statement {
  margin: 1rem 0 0;
  font-size: $fs-16;
}

figure {

  &.lead {
    position: relative;
    overflow: hidden;
    margin-bottom: 0;
    max-height: 350px;
    background: $clr-4;

    &:before { // stops page jump
      position: relative;
      z-index: 1;
      display: block;
      padding-top: 28.1%;
      background: $clr-4;
      content: "";
    }

    img {
      position: absolute;
      top: 0; left: 0;
      z-index: 2;
    }
  }

  &.expanded {
    overflow: hidden;
    border-radius: $border-radius;

    @include bp-m-less {
      margin-left: -$gutter;
      margin-right: -$gutter;
      border-radius: 0;
    }
  }

  img {
    display: block;
    width: 100%;
  }
}

p.navigation-jump {
  text-align: center;
  @include bp-m {@include hide-screen();}
}

span.brand-colour {
  display: inline-block;
  padding: 0 0.25rem;
  background: $clr-b1;
  color: white;
}
