
// article pages

body.article {
  article figure.lead + div.strip > div.wrap {
    h1 {@extend .h1-2;}
    > p:nth-of-type(1):not([class]) {@extend .lead;}
    > ul {@extend .disc;}
    > ol {@extend .decimal;}
    
    div.meta {
      // display: flex;
      // align-items: flex-end;
      // flex-wrap: wrap;
      margin: 0 0 $vmargin;
      border-bottom: 5px solid $clr-b1;

      ul {
        //flex: 1 0 auto;
        margin: 0 1rem ($vmargin/2) 0;
        list-style: none;
        font-size: $fs-18;

        a {
          position: relative;
          text-decoration: none;
          font-weight: 600;
          color: $clr-1;

          @include bp-m {
            &:hover,
            &:focus {color: $clr-b1;}
          }
        }
      }

      //div.other-element {flex: 0 0 auto;}
    }
  }
}

// guide pages

// body.guide div.page-intro h1 {@extend .h1-2;}

div.guide-content > div.wrap > div.row {
  > div.col-4 {
    @include bp-m {order: 1;}
  }

  > div.col-8 {
    @include bp-m {order: 2;}
    > p:nth-of-type(1) {@extend .lead;}
    > ul:not([class]) {@extend .disc;}
    > ol:not([class]) {@extend .decimal;}
  }

  @include bp-m-less {
    p.back-top {@include hide-screen();}
  }
}

// training pages

div.training-content {
  > p:nth-of-type(1):not([class]) {@extend .lead;}
  > ul:not([class]) {@extend .checked;}
  > ol:not([class]) {@extend .numbered-blocks;}
}

// strip

div.strip {
  padding: $spacing 0;
  @include bp-m {padding: ($spacing*2) 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);
    @include bp-m {padding: ($spacing*3) 0 ($spacing*2);}
    h2 {
      @extend .h1-2;
    }
  }

  &.latest,
  &.guide,
  &.why-ms {
    p.lead span {display: block;}
  }

  &.guide p.lead a {color: white;}

  &.alt {padding-top: 0;}

  &.style-1,
  &.style-2,
  &.style-3,
  &.style-4,
  &.style-5,
  &.style-6 {
    position: relative;
    color: white;

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      background: url(../../assets/images/content/other/polygon-bg.jpg) no-repeat 50%;
      background-size: cover;
      content: "";
      opacity: 0.2;
    }

    div.wrap {
      position: relative;
      z-index: 2;
    }
  }

  &.style-1 {background: $clr-b1;}
  &.style-2 {background: $clr-b2;} 
  &.style-3 {background: $clr-b3;} 
  &.style-4 {background: $clr-b4;} 
  &.style-5 {background: $clr-b5;} 
  &.style-6 {background: #F8F9FF; color: $clr-1;} 

  &.style-2,
  &.style-3 {
    &:before {transform: rotate(180deg);}
  }

  &.contact {
    text-align: center;
    background: $clr-2;
    color: white;

    h2 {@extend .lead;}

    div.col > p {
      @include bp-m {
        margin: 0;
      }

      a.icon-phone {
        @extend .h2;
        text-decoration: none;
        color: white;

        &:before {
          margin-right: 0.5rem;
          font-size: $fs-25;
        }
      }
    }
  }

  &.consulting-intro {
    padding: ($spacing*2) 0;
    text-align: center;

    @include bp-m {padding: ($spacing*2) 0;}

    &:before {background-position: 0 20%;}

    h1 {
      @extend .h1-2;
      margin-bottom: 1rem;

      strong {
        display: inline-block;
        margin-bottom: 1rem;
        border-radius: 2rem;
        padding: 0.25rem 1rem;
        background: rgba(black,0.3);
        color: white;
        font-size: $fs-16;
        font-weight: 400;

        @include bp-m {font-size: $fs-18;}
      }

      span {
        @include bp-m {display: block;}
      }
    }

    p {
      @extend .lead;
      margin: 0 auto;
      max-width: none;

      @include bp-m {
        max-width: 800px;
      }
    }
  }

  &.training-intro {
    padding: ($spacing*3) 0 $spacing;
    @include bp-m {padding: ($spacing*4) 0 ($spacing*2);}

    &:before {background-position: 0 20%;}

    h1 {
      @extend .h1-2;
      margin-bottom: 0;
    }

    p {
      @extend .lead;
      margin-bottom: 0;
      max-width: none;

      @include bp-m {
        max-width: 60%;
      }
    }
  }

  &.guide-intro {
    padding: ($spacing*2) 0;
    text-align: center;
    @include bp-m {padding-top: ($spacing*4);}

    &:before {background-position: 0 20%;}

    h1 {
      margin-bottom: $vmargin/2;

      body:not([class="guide index"]) & {@extend .h1-2;}
    }

    p.lead {
      @extend .col-8;
      margin: 0 auto;
    }
  }

  &.landing-intro {
    padding: 0;

    div.row {
      @include bp-m {
        > div:first-child {
          display: flex;
          align-items: center;
        }

        > div:last-child {
          display: flex;
        }
      }
    }

    div.video-wrap {
      flex: 1;
      margin: 0 (-$gutter);
      background: black;
      box-shadow: 0 0 10px 5px rgba(black,0.1);

      @include bp-m {margin: ($spacing*2) 0;}
    }

    div.text {
      padding-top: $spacing;
      padding-bottom: $spacing;

      h1 {
        margin-bottom: $vmargin/2;
        font-size: $fs-40;
      }

      h2 {
        font-weight: 400;
        font-size: $fs-20;
        line-height: $lh-general;
      }

      ul.buttons {
        margin-bottom: 0;

        li {
          display: inline-block;
          margin: 0 0.25rem 0.5rem 0;
        }
      }
    }
  }

  // call to actions

  &.cta {
    position: relative;
    z-index: 50;
    padding: $spacing 0;
    background-color: $clr-2;
    color: white;

    @include bp-m {padding: ($spacing*1.5) 0;}

    div.wrap {
      text-align: center;

      @include bp-m {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: left;
      }

      @include bp-l {align-items: flex-end;}

      h2 {
        @extend .h3;
        flex: 0 1 auto;
        align-self: center;
        margin: 0;
        font-weight: 300;

        span {
          font-size: $fs-20;
          
          span {
            @include bp-s {white-space: nowrap;}
          }
        }
      }

      > p {
        flex: 0 0 auto;
        margin: $vmargin auto 0;
        align-self: center;

        @include bp-m {margin: 0 0 0 $spacing;}
      }
    }
    
    @include bp-l {
      &.spr-matcher,
      &.whitepaper-1,
      &.whitepaper-2,
      &.whitepaper-3 {

        span.image {
          flex: 0 0 auto;
          display: block;
          position: relative;
          width: 280px;
        
          &:before {
            position: absolute;
            left: -($spacing); bottom: -($spacing*1.5);
            padding-top: 69%;
            width: 100%;
            background: no-repeat 50% 100%;
            background-size: 100% auto;
            content: "";
          }
        }

        div.wrap h2 {flex: 1 1 auto;}
      }

      &.spr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/spr-matcher.png);}
      &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
      &.whitepaper-2 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-2.png);}
      &.whitepaper-3 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
    }

    &.landing {
      div.wrap {
        p {
          @extend .h3;
          flex: 1 1 auto;
          align-self: center;
          margin: ($vmargin/2) 0 0;
          font-weight: 300;

          @include bp-m {margin: 0 0 0 $vmargin;} 

          a {
            font-weight: 700;
            color: white;
            text-decoration: none;
            white-space: nowrap;

            @include bp-m {
              &:hover,
              &:focus {text-decoration: underline;}
            }
          }  
        }

        a.button {
          flex: 0 0 auto;
          margin: 0 auto;
          align-self: center;
        }
      }
    }
  }
}

// page intro

div.page-intro {
  padding: $spacing 0;
  text-align: center;

  @include bp-s {padding: ($spacing*2) 0;}
  @include bp-m {padding-top: ($spacing*3);}

  p.lead {
    margin: 0 auto $vmargin;
    max-width: none;

    @include bp-m {
      margin: 0 auto $vmargin;
      max-width: 75%;
      font-size: $fs-30;
    }

    @include bp-l {margin-top: $vmargin;}
  }

  div.video-wrap {
    margin: $vmargin auto;
    max-width: 600px;

    @include bp-m {
      margin-top: $vmargin*2;
    }
  }
}

// home intro

div.home-intro {
  @extend .page-intro;
  overflow: hidden;
  position: relative;
  padding-bottom: $spacing*14;
  background: white;

  @include bp-s {padding-bottom: $spacing*12;}
  @include bp-m {padding-bottom: $spacing*15;}
  @include bp-l {padding-bottom: $spacing*20;}

  h1 {
    position: relative;
    z-index: 10;
    margin-bottom: ($vmargin);
    @include bp-s {margin-bottom: $vmargin*2;}
    @include bp-l {margin-bottom: $vmargin*2.5;}
    span {display: block;}
  }

  div.intro-funnels {

    ol {
      display: flex;
      margin: 0 (-$gutter);
      @include bp-s {margin: 0;}

      li {
        flex: 1 1 $col-4;

        div.content {

          &:before {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 1;
            background: white url(../../assets/images/content/other/polygon-bg.jpg) no-repeat 50%;
            background-size: cover;
            box-shadow: inset 0 50px 50px 0 white; // hides the hard nav line during transition
            content: "";
            opacity: 0;
            transition: opacity 1s;
          }

          &:after {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 2;
            content: "";
            opacity: 0;
            transition: opacity 1s;
          }

          &.intro-funnel-1 {
            &:after {background-image: linear-gradient(to bottom, white 0%, rgba($clr-b2,0.7) 60%);}
            label {border-radius: $border-radius 0 0 $border-radius;}
          }

          &.intro-funnel-2 {
            &:after {background-image: linear-gradient(to bottom, white 0%, rgba($clr-b3,0.7) 60%);}
          }

          &.intro-funnel-3 {
            &:after {background-image: linear-gradient(to bottom, white 0%, rgba($clr-b4,0.7) 60%);}
            label {border-radius: 0 $border-radius $border-radius 0;}
          }

          label {
            overflow: hidden;
            position: relative;
            z-index: 10;
            display: block;
            padding: ($spacing/2) 0;
            background: rgba(white,0.7);
            font-size: $fs-20;
            font-weight: 300;
            cursor: pointer;
            user-select: none;
            transition: background-color 0.7s, color 0.35s;

            @include bp-s {font-size: $fs-25;}

            @include bp-m {
              padding: $spacing 0;
              font-size: $fs-35;

              &:hover,
              &:focus {background: rgba(white,0.85);}
            }

            @include bp-l {font-size: $fs-40;}
          }

          div.text {
            position: absolute;
            left: 0; right: 0;
            z-index: 10;
            margin-top: $vmargin*1.5;
            padding: 0 $spacing;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.25s, margin-top 0s 0.25s, visibility 0s 0.25s;

            h2 {
              margin: 0 auto $vmargin;
              max-width: 970px;
              font-weight: 300;
              font-size: $fs-25;
              line-height: $lh-general;
              color: white;

              @include bp-m {
                margin-bottom: $vmargin*2;
                font-size: $fs-30;
              }

              @include bp-l {font-size: $fs-40;}
            }

            p {
              margin: 0 auto;
              font-size: $fs-22;
              @include bp-m {font-size: $fs-25;}
              @include bp-l {font-size: $fs-30;}

              a.button {font-weight: 400;}
            }
          }
        }
      }
    }

    ol li input:not(#iefix) {
      @include hide-screen();

      &:checked + div.content {

        &:before {opacity: 1;}
        &:after {opacity: 1;}

        label {
          background: $clr-2;
          color: white;
          transition: background-color 1s, color 1s;

          + div.text {
            margin-top: $vmargin*1.5;
            opacity: 1;
            visibility: visible;
            transition: opacity 0.75s 0.25s, visibility 0s 0.25s;

            @include bp-m {margin-top: ($vmargin*2.5);}
          }
        }
      }
    }
  }
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}

      &.col-4,
      &.col-6 {
        @include bp-m {&:nth-child(2) {margin-top: 0;}}
      }

      &.col-4 {
        @include bp-m {&:nth-child(3) {margin-top: 0;}}
      }
    }
  }
}


div.wrap > h2 + div.general-wrap,
div.wrap > h2 + p.lead + div.general-wrap {
  margin-top: $vmargin*1.5;
  @include bp-m {margin-top: $vmargin*2;}
}


// announcement

div.announcement {
  padding: ($spacing/1.5) 0;
  background: $clr-2;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {
    font-weight: 700;
    color: white;
  }
}

// box 

div.box {
  overflow: hidden;
  margin: 0 0 $vmargin;
  border-radius: $border-radius;
  background: $clr-4;

  > div:first-of-type {margin-top: 0;}
  > :last-child {margin-bottom: 0;}

  div.col-4 & {font-size: $fs-18;}

  h2 {
    @extend .h3;
    margin: 0;
    background: $clr-2;

    body.consulting & {background: $clr-b2;}
    body.training & {background: $clr-b3;}
    body.guide & {background: $clr-b4;}
    body.landing &,
    &.alt {background: $clr-b1;}

    a,
    span {
      display: block;
      padding: ($spacing/2) $spacing;
      color: white;
      text-decoration: none;
      transition: background-color 0.2s;
    }

    a {
      @include bp-m {
        &:hover,
        &:focus {background: rgba(black,0.2);}
      }
    }
  }

  div.content {
    padding: $spacing;
    > :last-child {margin-bottom: 0;}
  }

  // secondary nagivation

  nav {

    @include bp-m {padding-bottom: 0.5rem;}

    ul {
      margin: 0;

      li {
        a {
          display: block;
          padding: 0.5rem $spacing;
          text-decoration: none;
          color: $clr-1;
          transition: background-color 0.2s, color 0.1s;

          @include bp-m {
            &:hover,
            &:focus {
              background: rgba(white,0.6);
              color: $clr-b1;
            }
          }

          &.current {
            background: rgba(white,0.6);
            color: $clr-b1;
            box-shadow: inset 5px 0 $clr-b1;
          }
        }

        ul {
          font-size: $fs-17;
          li a:before {content: "\02013\000A0";}
        }
      }
    }
  }
}

// iframe map

div.contact-map {
  border-bottom: 20px solid $clr-2;
}

iframe.map {
  display: block;
  border: 0;
  width: 100%;
  height: 300px;

  div.contact-map & {
    @include bp-l {height: 500px;}
  }

  body.landing &,
  body.landing-alt & {
    margin-bottom: $vmargin;
    @include bp-l {height: 350px;}
  }
}

// video wrap

div.video-wrap {
  position: relative;
  margin-bottom: $vmargin;
  background: $clr-4;

  &:before {
    display: block;
    padding-top: 56.3%;
    content: "";
  }

  iframe {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
  }
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-3;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  //form & {margin: 0;}

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-22;
  }

  &.error,
  &.success,
  &.warning,
  &.caution {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    //form & {margin-top: $vmargin;}

    &:before {
      @extend .icon-extension,.icon-exclamation;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation;
    }
  }
}

// contact form inline

input#contact-form-inline {
  @include hide-screen();

  &:checked {
    + div.row {
      label.contact-form-inline-cta:before {@extend .icon-minus-circle;}
      + div.contact-form-inline {display: block;}
    }
  }
}

div.contact-form-inline {
  display: none;
  margin: $vmargin auto 0;
  border-radius: $border-radius;
  padding: $spacing;
  max-width: 30rem;
  background: $clr-1;
}

// share links

div.share {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-bottom: $vmargin;
  border-top: 5px solid $clr-b1;
  padding-top: $spacing;

  @include bp-m {margin-bottom: 0;}

  ul.share-options {flex: 1 0 auto;}

  p {
    flex: 0 0 auto;
    margin-bottom: 0;

    a {
      display: block;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      line-height: 1.9rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      font-size: $fs-14;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
  }
}

// PMCF process

div.strip.pmcf-process {
  text-align: center;
  @include bp-m {text-align: left;}

  div.row {
    @include bp-m {align-items: center;}
  }

  h2 {text-align: center;}

  figure {
    text-align: center;

    @include bp-m {margin-bottom: 0;}

    img {
      display: inline-block;
      max-width: 229px;
    }
  }
}